import React, { Component } from 'react';

class Filters extends Component {

  render() {
    const score = "Score (>)";
    return (
      <div className="filter w-154">
        <h1 className="title">Filter</h1>
        <div className="table">
          <div>Location</div>
          <input placeholder="Bus Stop" type="text" pattern="[A-Za-z]*" onChange={e=>this.props.handleLocation(e.target.value)}/>
        </div>
        <div className="table">
          <div>Label</div>
          <input placeholder="Bike" type="text" pattern="[A-Za-z]*" onChange={e=>this.props.handlePrediction(e.target.value)}/>
        </div>
        <div className="table">
          <div>{score}</div>
          <input placeholder="10" type="number" pattern="[0-9]*" onChange={e=>this.props.handleScore(e.target.value)}/>
        </div>
         <div className="column">
          <div style={{'textAlign':'center'}}>Date</div>
          <input type="date" onChange={e=>this.props.handleDate(e.target.value)}/>
        </div>
      </div>
    );
  }
}

export default Filters;
