import React, { Component } from 'react';
import ImageContainer from '../components/imageContainer';

class ImageContainerList extends Component {

    render() {
      return (
        <div className="flex-grow-1">
          <h1 className="title">Image</h1>
          <ol className="imageContainerList flex">
          {this.props.imageIndex.map(i=><li key={i}><ImageContainer index={i} event_data={this.props.event_data} handleImage={this.props.handleImage} filterObj={this.props.filterObj}/></li>)}
          </ol>
        </div>
      );
    }
}

export default ImageContainerList;
