import React, { Component } from 'react';
import TimeBtn from '../components/timeBtn';

class TimeBtnList extends Component {

  render() {
    return (
      <div className="w-154">
        <h1 className="title">Date</h1> 
        <ol className="timeBtnList">
        {this.props.event_data.map(i=><li key={i.timestamp}>
          <TimeBtn event_data={i} index={parseInt(this.props.event_data.indexOf(i))} handleImage={this.props.handleImage} imageIndex={this.props.imageIndex} filterObj={this.props.filterObj}/>
          </li>)}
        </ol>
      </div>
    );
  }
}

export default TimeBtnList;
