import React, { Component } from 'react';
import Prediction from '../components/prediction';

class ImageContainer extends Component {

	constructor(props) {
		super(props);
		this.state = {
			event_data: props.event_data[props.index] //list of events => single event 
			/*
			this.state.event_data = {
			  videoStream, 
			  timestamp, 
			  imageSource,
			  predictions: [{boundingBox: , scores :
			                            {top,       [{score,
			                             bottom,      label,
			                             height,    }]
			                             width}     
			               }],
			  timeShort, (new)
			  isMorning, (new)
			  timeLong,  (new)
			  date		 (new)
			*/

		}
	}

	matchLocation() {
	/*
		False:
			- user location is not "" AND does NOT match
		True: 
			- otherwise  
	*/
		let userLocation = this.props.filterObj.location;
		if (userLocation !== "") {
			userLocation = userLocation.toLowerCase().trim();
			const pat = new RegExp(".*" + userLocation + ".*");
			if (!pat.test(this.state.event_data.videoStream.toLowerCase().trim())) {
				return false;
			}
		}
		return true; 
	}

	shouldRender() {
	/*
		True: 
			- score is 0 
			- prediction is ""
			- score > user's score AND prediction matches user's prediction
		False:
			- location does NOT match 
			- otherwise 
	*/
		if (!this.matchLocation()) {
			return false;
		}

		const userScore = this.props.filterObj.score;
		let userPrediction = this.props.filterObj.prediction;
		if ( userScore === 0 ||  userPrediction === "" || userPrediction.trim() === "") {
			return true;
		}

		let shouldRender = false;
		const {predictions} = this.state.event_data
		userPrediction = userPrediction.toLowerCase().trim(); //case insensitive, clean string 

		const pattern = new RegExp(".*" + userPrediction + ".*"); //substring match of user's prediction 
		let score;
		let prediction;

		for (let p in predictions) {
			for (let s in predictions[p].scores) {
				score = predictions[p].scores[s].score; 
				prediction = predictions[p].scores[s].label.toLowerCase().trim();
				if (pattern.test(prediction) && score >= userScore) {
					return true
				}

			}
		}
		return shouldRender;
	}

  render() {
  	const {event_data} = this.state;
  	const {predictions} = event_data;
  	if (!this.shouldRender()) {
  		return null;
  	}
    return (
    	<div className="column">
    		<h1 className="flex">
    			<div>{event_data.videoStream + " - " + event_data.timeShort}</div>
    			<svg className="cursorPointer" onClick={()=>this.props.handleImage(this.props.index)}version="1.1" viewBox="0 0 15.642 15.642" enableBackground="new 0 0 15.642 15.642" width="15px" height="15px">
				  <path fillRule="evenodd" d="M8.882,7.821l6.541-6.541c0.293-0.293,0.293-0.768,0-1.061  c-0.293-0.293-0.768-0.293-1.061,0L7.821,6.76L1.28,0.22c-0.293-0.293-0.768-0.293-1.061,0c-0.293,0.293-0.293,0.768,0,1.061  l6.541,6.541L0.22,14.362c-0.293,0.293-0.293,0.768,0,1.061c0.147,0.146,0.338,0.22,0.53,0.22s0.384-0.073,0.53-0.22l6.541-6.541  l6.541,6.541c0.147,0.146,0.338,0.22,0.53,0.22c0.192,0,0.384-0.073,0.53-0.22c0.293-0.293,0.293-0.768,0-1.061L8.882,7.821z" fill="#FFFFFF"/>
				</svg>

    		</h1>
	    	<div className="imageContainer">
	    		<img src={event_data.imageSource} className="w-400" alt="object"/>
		    	<div>{predictions.map(prediction=><Prediction key={predictions.indexOf(prediction)} prediction={prediction} event_data={event_data}/>)}</div>
	    	</div>
    	</div>
    );
  }
}

export default ImageContainer;
