import React, { Component } from 'react';
/*
this.props.event_data = {
  videoStream, 
  timestamp, 
  imageSource,
  predictions: [{boundingBox: , scores :
                            {top,       [{score,
                             bottom,      label,
                             height,    }]
                             width}     
               }],
  timeShort, (new)
  isMorning, (new)
  timeLong,  (new)
  date       (new)
*/
class TimeBtn extends Component {
	constructor(props) {
		super(props);
		const btn = (props.event_data.isMorning) ? "am-btn" : "pm-btn";
		this.state = {
			timeLong: props.event_data.timeLong,
			btn: btn
		}
	}

	matchLocation() {
		/*
		False:
			- user location is not "" AND does NOT match
		True: 
			- otherwise  
		*/
		let userLocation = this.props.filterObj.location;
		if (userLocation !== "") {
			userLocation = userLocation.toLowerCase().trim();
			const pattern = new RegExp(".*" + userLocation + ".*");
			if (!pattern.test(this.props.event_data.videoStream.toLowerCase().trim())) {
				return false;
			}
		}
		return true; 
	}

	matchDate() {
		/*
		False:
			- user date is not null AND does NOT match
		True: 
			- otherwise  
		*/
		let userDate = this.props.filterObj.date;
		const date = this.props.event_data.date;
		if (userDate) {
			console.log("date: ", date.getDate())
			if (userDate.day !== date.getDate()) {
				return false;
			}
			if (userDate.month !== date.getMonth() + 1) { 
				return false;
			}
			if (userDate.year !== date.getFullYear()) {
				return false;
			}
		}
		return true; 
	}

	shouldRender() {
		/*
		True: 
			- score is 0 
			- prediction is ""
			- score > user's score AND prediction matches user's prediction 
		False:
			- otherwise 
		*/

		if(!this.matchLocation()) {
			return false;
		}

		if(!this.matchDate()) {
			return false;
		}
		
		const userScore = this.props.filterObj.score;
		let userPrediction = this.props.filterObj.prediction;
		if ( userScore === 0 ||  userPrediction === "" || userPrediction.trim() === "") {
			return true;
		}

		let shouldRender = false;
		const {predictions} = this.props.event_data
		userPrediction = userPrediction.toLowerCase().trim(); //case insensitive, clean string 

		const pattern = new RegExp(".*" + userPrediction + ".*"); //substring match of user's prediction 
		let score;
		let prediction;

		for (let p in predictions) {
			for (let s in predictions[p].scores) {
				score = predictions[p].scores[s].score; 
				prediction = predictions[p].scores[s].label.toLowerCase().trim(); 
				if (pattern.test(prediction) && score >= userScore) {
					return true
				}

			}
		}
		return shouldRender;
	}

	render() {
		const open = (this.props.imageIndex.indexOf(this.props.index) !== -1) ? "open" : "";
		const btnStyle = "timestamp cursorPointer table " + open + " " + this.state.btn;
		
		if (!this.shouldRender()) {
			return null;
		}

		return (
			<label className="timeBtn">
				<div className={btnStyle} onClick={()=>this.props.handleImage(this.props.index)}> 
				  <div>
				  	{this.state.timeLong}
				  </div>
				</div>
			</label>
		);
	}
}

export default TimeBtn;
