import React, { Component } from 'react';
import PredictionLabel from '../components/predictionLabel';

/*
this.props.prediction = {
  videoStream, 
  timestamp, 
  imageSource,
  predictions: [{boundingBox: , scores :
                            {top,       [{score,
                             bottom,      label,
                             height,    }]
                             width}     
               }],
  timeShort, (new)
  isMorning, (new)
  timeLong,  (new)
  date 		 (new)
*/

class Prediction extends Component {
	constructor(props) {
		super(props);
		const borderClass = (props.event_data.isMorning) ?  "am-border" : "pm-border";
		const {boundingBox} = props.prediction;
	  	const styleBorder = {
	  		top: boundingBox.top*100 + "%",
	  		left: boundingBox.left*100 + "%",
	  		height: boundingBox.height*100 + "%",
	  		width: boundingBox.width*100 + "%"
	  	}
		this.state = {
			borderClass : "prediction cursorPointer " + borderClass,
			styleBorder : styleBorder,
			checkmarkClass: new Array(props.prediction.scores.length).fill("")

		} 
	}

	handleCheck(index) {
		let checkmarkClass = new Array(this.props.prediction.scores.length).fill(""); //reset 
		if (this.state.checkmarkClass[index] === "") {
			checkmarkClass[index] = "open";
		}
		this.setState({
			checkmarkClass: checkmarkClass
		});
	}

  render() {
  	const {scores} = this.props.prediction;
    return (
    	<div className={this.state.borderClass} style={this.state.styleBorder}>
	    	<div className="scoreContainer">
		    	<h1>Scores</h1>
		    	<ul>
		    		{scores.map(s=><li className="flex" key={scores.indexOf(s)}>
		    			<PredictionLabel s={s} 
		    							 checkmarkClass={this.state.checkmarkClass} 
		    							 index={scores.indexOf(s)}
		    							 handleCheck={this.handleCheck.bind(this)}/>
		    			</li>)}
		    	</ul>
		    	</div>
    	</div>
    );
  }
}

export default Prediction;
