import React, { Component, Fragment } from 'react';
import './App.css';
import mockResponse from './event_data.js'; //fake API request 
import TimeBtnList from './components/timeBtnList';
import ImageContainerList from './components/imageContainerList';
import Filters from './components/filters';

/*
event_data = {
  videoStream, 
  timestamp, 
  imageSource,
  predictions: [{boundingBox: , scores :
                            {top,       [{score,
                             bottom,      label,
                             height,    }]
                             width}     
               }],
  timeShort, (new)
  isMorning, (new)
  timeLong,  (new)
  date       (new)
*/

class App extends Component {

  constructor() {
    super();
    let event_data = mockResponse.mockResponse.events.slice(); //copy 
    event_data = this.sortByKey(event_data, "timestamp");
    this.readableDatetime(event_data);
    this.state = {
      event_data : event_data,
      imageIndex : [], //index of currently open images 
      score : 0, //assumes all scores > 0
      prediction: "", 
      location: "",
      date: null
    }
  }

  sortByKey(array, key) {
  /*
  ([]) array 
  (string) key
    -helper function for sorting an array by key 
  */
    return array.sort(function(a, b) {
        var x = a[key]; var y = b[key];
        return ((x < y) ? -1 : ((x > y) ? 1 : 0));
    });
  }

  sortNumber(a,b) {
  /*
  (int) a
  (int) b
    - priority function for numerical sort()
  */
    a = parseInt(a);
    b = parseInt(b);
    return a - b;
  }

  readableDatetime(event_data) {
  /*
  ([]) event_data
    - adds timeShort and timeLong to all objects 
  */
    let date; 
    let i;
    for (i in event_data) {
      date = new Date(event_data[i].timestamp*1000);
      event_data[i]["timeShort"] = this.convert_12_hour(date);
      event_data[i]["isMorning"] = (event_data[i].timeShort.slice(-2) === "AM"); 
      event_data[i]["timeLong"] = this.convertLongDate(date);
      event_data[i]["date"] = date; 
    }
  }


  convert_12_hour(date) {
  /*
  (Date Object) date 
    - converts 24 time to 12 hour time 
  */
    const hour = date.getHours();
    const minute = date.getMinutes();

    const min = (minute >= 10) ? minute : "0" + minute;  
    if (hour < 12) {
      return hour + ":" + min + " AM" 
    } else if (hour === 12) {
      return hour + ":" + min + " PM"
    } else if (hour > 12) {
      return hour%12 + ":" + min + " PM"
    }
  }


  convertLongDate(date) {
  /*
  (dateObj) date 
    - returns DAY OF WEEK, MONTH, DAY OF MONTH, YEAR 
  */
    const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    const day = days[date.getDay()];
    const month = months[date.getMonth()];
    return day + ", " + month + " " + date.getDate() + ", " + date.getFullYear();
  }

  handleImage(index) {
  /*
  (int) index: an index of an image 
    - Adds or remove index from state 'imageIndex' 
    - Handles state change for timeBtn and imageContainer 
  */
    let newList = this.state.imageIndex.slice();
    const imageIndex = newList.indexOf(index);
    if (imageIndex === -1) {
      newList.push(index);
    } else {
      newList.splice(imageIndex, 1);
    }
    newList.sort(this.sortNumber);
    this.setState({
      imageIndex: newList
    })
  }

  handleScore(score) {
  /*
  (int) score: user predicted score 
    - handles state change for Filters
  */
      if (score) {
        this.setState({
          score: score
        });
      } else {
        this.setState({
          score: 0 //reset  
        });
      } 
  }

  handlePrediction(prediction) {
  /*
  (string) prediction 
    - handles state change for Filters
  */
    if (prediction) {
      this.setState({
        prediction: prediction
      });
    } else {
      this.setState({
        prediction: "" //reset 
      });
    }
  }

  handleLocation(location) {
  /*
  (string) location 
    - handles state change for Filters
  */
    if (location) {
      this.setState({
        location: location
      });
    } else {
      this.setState({
        location: "" //reset 
      });
    }
  }

  handleDate(date) {
  /*
  (Date Obj) Date
    - handles state change for Filters
  */
    if (date) {
      const year = parseInt(date.slice(0,4));
      const month = parseInt(date.slice(5,7));
      const day = parseInt(date.slice(8));
      this.setState({
        date: {
          year: year,
          month: month,
          day: day
        }
      });
    } else {
      this.setState({
        date: null //reset 
      });
    }
  }  

  render() {
    return (
      <Fragment>
        <Filters handleScore={this.handleScore.bind(this)} 
                 handlePrediction={this.handlePrediction.bind(this)}
                 handleLocation={this.handleLocation.bind(this)}
                 handleDate={this.handleDate.bind(this)}/>
        <TimeBtnList event_data={this.state.event_data} 
                     handleImage={this.handleImage.bind(this)}
                     imageIndex={this.state.imageIndex} 
                     filterObj={{"score":this.state.score, 
                                 "prediction":this.state.prediction, 
                                 "location":this.state.location,
                                 "date": this.state.date
                                }}/>
        <ImageContainerList event_data={this.state.event_data}
                            handleImage={this.handleImage.bind(this)} 
                            imageIndex={this.state.imageIndex} 
                            filterObj={{"score":this.state.score, 
                                       "prediction":this.state.prediction, 
                                       "location":this.state.location,
                                       "date": this.state.date}}/>
      </Fragment>
    );
  }
}

export default App;
